/* ======= Services style ======= */
.services .item {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding: 0;
    margin: 0;
    float: left;
    box-sizing: border-box
}

.services .item:hover .item-inner {
    -webkit-clip-path: inset(15px 15px 15px 15px);
    clip-path: inset(15px 15px 15px 15px)
}

.services .item .item-inner {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: middle;
    background-color: #f9f9f9;
    -webkit-background-size: cover;
    background-size: cover;
    -webkit-clip-path: inset(0 0 0 0);
    clip-path: inset(0 0 0 0);
    -webkit-transition: all 1.25s cubic-bezier(.01, .71, .26, .94);
    -moz-transition: all 1.25s cubic-bezier(.01, .71, .26, .94);
    transition: all 1.25s cubic-bezier(.01, .71, .26, .94)
}

.services .item:hover img {
    -webkit-filter: none;
    filter: none;
    -webkit-transform: scale(1.09, 1.09);
    transform: scale(1.09, 1.09);
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -o-transition: all 1s ease;
    -ms-transition: all 1s ease;
    transition: all 1s ease;
}

.services .item:hover .con {
    bottom: 0;
}

.services .item img {
    -webkit-transition: all .5s;
    transition: all .5s;
}

.services .item .con {
    padding: 30px;
    position: absolute;
    bottom: -55px;
    left: 0;
    width: 100%;
    -webkit-transition: all .3s;
    transition: all .3s;
    text-align: center;
}

.services .item .con .category {
    margin-bottom: 0;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    text-transform: uppercase;
    color: #f96f00;
    font-weight: 400;
    letter-spacing: 5px;
}

.services .item .con .category a {
    margin-right: 5px;
    position: relative;
}

.services .item .con .category a:last-child {
    margin-right: 0;
}

.services .item .con .category a:last-child:after {
    display: none;
}

.services .item .con h5 {
    color: #fff !important;
    font-size: 20px;
    margin-bottom: 30px;
    font-family: 'Didact Gothic', sans-serif;
}

.services .item .con p {
    color: #fff;
    font-size: 15px;
}

.services .item .con i {
    color: #fff;
    font-size: 20px;
}

.services .item .con i:hover {
